import React,{useState} from 'react'
import { motion } from 'framer-motion/dist/framer-motion'

function ItemCard(props) {
  // 
  const {name, price, image ,materials} = props

  const [cardOpen, setCardOpen] = useState(false)

  return (
    <motion.div layout initial={{opacity:0}} animate={{opacity:1}} exit={{opacity:0}} className={`itemCard ${cardOpen ? "open" : "close"}`}>
        <h1 className='card-title'>{name}</h1>

        <p className='card-price'>{price} <small>تومان</small></p>
        <img className='card-img' src={image} alt="" />

        <button className="card-buttom" onClick={()=>setCardOpen(!cardOpen)}>
            <i className="fas fa-angle-down"></i>
        </button>

        <ul className="materials">
          {materials?.map((item, index)=>(
            <li key={index}>{item}</li>
          ))}
        </ul>
    </motion.div>

    
  )
}

export default ItemCard