import React, {useState, useEffect} from 'react'
import { motion } from 'framer-motion/dist/framer-motion'
import { Link } from 'react-router-dom'

import ItemCard from "../components/ItemCard"

function Menu() {

  const [activeCat, setActiveCat] = useState(null)
  const [activeNav, setActiveNav] = useState(null)

  const [items, setItems] = useState(null)
  const [categories, setCategories] = useState(null)

  const filterdItems = activeCat === activeNav ? items?.filter(item => item.category.parent === activeCat) : items?.filter(item => item.category.name === activeCat)


  const handleNav = (x) =>{
    setActiveCat(x)
    setActiveNav(x)
  }

  useEffect(()=>{
    fetch('http://192.168.1.3:8000/api/items/')
    .then(res => res.json())
    .then(data => setItems(data))
    .catch(err=>{
      return
    })
    fetch('http://192.168.1.3:8000/api/categories/')
    .then(res => res.json())
    .then(data => {
      setCategories(data)
      setActiveNav(data[0].name)
      setActiveCat(data[0].name)
    })
    .catch(err=>{
      return
    })
  },[])


  return (
    <>
    <aside className='main-aside'>

    <header >
        <Link to="/">
        <h1>8</h1>
        <p><small>O’Clock Café</small></p>
        </Link>
    </header>

    <nav className='main-nav'>
        {categories?.map((cat, index)=>(
          !cat.parent && <button key={index} className={activeNav === cat.name ? "active" : undefined} onClick={()=>handleNav(cat.name)}>{cat.name}</button>
        ))}
    </nav>

    </aside>
    <main className='menu-view'>

        <h1 className="menu-header">منوی کافه</h1>
        <hr />

        <ul  className='subCat'>
          {categories?.filter(cat => activeNav === cat.parent).length>0 && <li><button className={activeCat === activeNav ? "active" : undefined} onClick={()=>setActiveCat(activeNav)}>همه</button></li>}
          {categories?.map((cat, index)=>(
            activeNav === cat.parent &&
            <li key={index}><button className={activeCat === cat.name ? "active" : undefined} onClick={()=>setActiveCat(cat.name)}>{cat.name}</button></li>
          ))}
        </ul>


        <motion.section layout  className="cardList">

          {filterdItems?.map(item => (
            <ItemCard key={item.id} name={item.name} price={item.price} image={item.image} materials={item.materials}/>
          ))}
          
        </motion.section>

    </main>
    </>
  )
}

export default Menu