import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

import Home from  "./views/Home"
import Menu from  "./views/Menu"


function App() {
  return (
    <Router>
      <Switch>
        <Route exact path= "/" ><Home/></Route>
        <Route path= "/menu" ><Menu/></Route>

      </Switch>
    </Router>
  );
}

export default App;
